export default {
	data() {
		return {
			hasForm: true,
			formList: [{
				label: '商会名称',
				val: '',
				name: 'chamber_name',
				type: 'text',
				length: 20
			}, {
				label: '姓名',
				val: '',
				name: 'real_name',
				type: 'text',
				length: 15
			}, {
				label: '手机号',
				val: '',
				name: 'phone',
				type: 'number',
				length: 11
			}, ]
		}
	},
	methods: {
		navBack() {
			this.$commonJs.navBack();
		},
		joinNow() {
			let Rules = [{
					name: 'chamber_name',
					type: 'required',
					errmsg: '请输入商会名称'
				},
				{
					name: 'real_name',
					type: 'required',
					errmsg: '请输入您的姓名'
				},
				{
					name: 'phone',
					type: 'required',
					errmsg: '请输入您的手机号'
				},
				{
					name: 'phone',
					type: 'phone',
					errmsg: '请输入正确的手机号'
				},
			];
			var formData = {};
			this.formList.forEach((item) => {
				formData[item.name] = item.val
			})
			let valLoginRes = this.$validate.validate(formData, Rules)
			if (!valLoginRes.isOk) {
				this.$commonJs.uniMsg(valLoginRes.errmsg)
				return false
			}
			// 入驻商会
			this.$apiJs.setChamberJoin(formData).then(res => {
				if (res) {
					this.hasForm = false;
				}
			})
		}
	},
	onLoad(option) {
		if(option.status){
			this.hasForm = false;
		}
	}

}
